"use client"

import { motion } from "framer-motion"

const companies = [
  "TechCorp",
  "DataFlow",
  "CloudScale",
  "AI Systems",
  "DevOps Pro"
]

export function SocialProof() {
  return (
    <section className="relative py-16 px-6 border-t border-neutral-900">
      <div className="mx-auto max-w-6xl">
        <motion.p
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          viewport={{ once: true }}
          className="text-center text-sm text-neutral-500 mb-8 uppercase tracking-wider"
        >
          Confiado por equipos de ingeniería en
        </motion.p>
        
        <motion.div
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          transition={{ duration: 0.8, delay: 0.2 }}
          viewport={{ once: true }}
          className="flex flex-wrap items-center justify-center gap-8 sm:gap-12"
        >
          {companies.map((company, index) => (
            <motion.div
              key={company}
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
              viewport={{ once: true }}
              className="flex items-center justify-center h-12 px-6 border border-neutral-800 rounded-lg bg-neutral-950/50"
            >
              <span className="text-sm font-medium text-neutral-400 tracking-wide">
                {company}
              </span>
            </motion.div>
          ))}
        </motion.div>
      </div>
    </section>
  )
}
