"use client"

import { motion, useInView } from "framer-motion"
import { useEffect, useRef, useState } from "react"

const terminalLines = [
  "$ aethermind deploy production",
  "✓ Building agent bundle...",
  "✓ Optimizing dependencies...",
  "✓ Deploying to edge network...",
  "",
  "✓ Agent live at: https://api.aethermind.ai/v1/agents/prod-x7k2",
  "",
  "$ aethermind status",
  "[INFO] Status: Running",
  "[INFO] Latency: <50ms | Uptime: 99.97%",
  "[INFO] Requests: 1.2M/day | Success rate: 99.8%",
  "",
  ">>> Ready for production traffic",
]

export function TerminalDemo() {
  const containerRef = useRef<HTMLDivElement>(null)
  const isInView = useInView(containerRef, { once: true, margin: "-100px" })
  const [displayedLines, setDisplayedLines] = useState<string[]>([])
  const [currentLine, setCurrentLine] = useState(0)

  useEffect(() => {
    if (!isInView) return

    if (currentLine < terminalLines.length) {
      const timer = setTimeout(() => {
        const line = terminalLines[currentLine]
        if (line !== undefined) {
          setDisplayedLines((prev) => [...prev, line])
        }
        setCurrentLine((prev) => prev + 1)
      }, 150)
      return () => clearTimeout(timer)
    }
  }, [isInView, currentLine])

  return (
    <div className="mx-auto max-w-4xl px-4">
      <div ref={containerRef}>
        <motion.div
          initial={{ opacity: 0, scale: 0.95 }}
          whileInView={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.8, delay: 0.2 }}
          viewport={{ once: true }}
          className="overflow-hidden rounded-xl border border-neutral-800 bg-neutral-950 shadow-2xl backdrop-blur-sm"
        >
          <div className="flex items-center gap-2 border-b border-neutral-800 bg-neutral-900 px-4 py-3">
            <div className="h-3 w-3 rounded-full bg-neutral-600" />
            <div className="h-3 w-3 rounded-full bg-neutral-500" />
            <div className="h-3 w-3 rounded-full bg-neutral-400" />
            <span className="ml-4 text-sm text-neutral-500">aethermind-cli</span>
          </div>

          <div className="h-96 overflow-auto p-4 font-mono text-sm">
            {displayedLines.map((line, i) => {
              if (!line) return <div key={i} className="h-4" />

              const safeReplace = (str: string, search: string, replacement: string) => {
                return str ? str.replace(search, replacement) : str
              }

              return (
                <motion.div
                  key={i}
                  initial={{ opacity: 0, x: -10 }}
                  animate={{ opacity: 1, x: 0 }}
                  className={`${
                    line.startsWith("$")
                      ? "text-white"
                      : line.startsWith("✓")
                        ? "text-neutral-300"
                        : line.startsWith("[INFO]")
                          ? "text-neutral-400"
                          : line.startsWith(">>>")
                            ? "text-neutral-200"
                            : "text-neutral-500"
                  }`}
                >
                  {line.startsWith("$") ? (
                    <>
                      <span className="text-white">$</span>
                      {safeReplace(line, "$", "")}
                    </>
                  ) : (
                    line
                  )}
                </motion.div>
              )
            })}
            {currentLine < terminalLines.length && <span className="inline-block h-4 w-2 animate-pulse bg-white" />}
          </div>
        </motion.div>
      </div>

      {/* Explanatory Text */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        whileInView={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6, delay: 0.8 }}
        viewport={{ once: true }}
        className="mt-12 text-center max-w-2xl mx-auto"
      >
        <h3 className="text-xl font-semibold text-white mb-3">Despliegue en segundos</h3>
        <p className="text-sm text-neutral-400 leading-relaxed">
          CLI potente para desplegar agentes a producción, gestionar versiones, y monitorear rendimiento desde la terminal.
        </p>
      </motion.div>
    </div>
  )
}
