"use client"

import { motion } from "framer-motion"

export function MissionSection() {
  return (
    <section className="relative py-32 px-6 border-t border-neutral-900">
      <div className="mx-auto max-w-3xl text-center">
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
        >
          <h2 className="text-4xl font-bold tracking-tight text-white sm:text-5xl mb-6">
            Construido para equipos de ingeniería
          </h2>
          <p className="text-lg text-neutral-400 leading-relaxed max-w-2xl mx-auto">
            Aethermind elimina la complejidad de construir sistemas de IA en producción. Desde prototipo hasta escala empresarial en días, no meses.
          </p>
        </motion.div>
      </div>
    </section>
  )
}
